      showVideoControls: true,
      
      // Êñá‰ª∂ÂàóË°®
      fileList: [
        { 
          id: 1,
          name: '‚ÄúÊ¥ûËßÅÂéÜÂè≤‚ÄùÈ°πÁõÆÂà∂Â≠¶‰π†Ê±áÊä•‰ºöËÆÆÁ®ã', 
          type: 'pdf', 
          targetPage: '/pages/issue1/issue1561'
        },
        { 
          id: 2,
