          type: 'docx', 
          targetPage: '/pages/issue1/issue1563'
        }
      ]
    };
  },
  
  computed: {
    // ÂΩìÂâçÊòæÁ§∫ÁöÑÂ™í‰Ωì
    currentMedia() {
      const group = this.customMediaGroups[this.currentGroupIndex];
      return group ? group.mediaItems[this.currentItemIndex] : {};
    },
    
    // ËÆ°ÁÆóÊÄªÂ™í‰ΩìÊï∞Èáè
    totalMediaCount() {
      return this.customMediaGroups.reduce((total, group) => {
        return total + group.mediaItems.length;
      }, 0);
    },
    
    // ËÆ°ÁÆóÂΩìÂâç‰ΩçÁΩÆ
    currentPosition() {
      let position = 0;
      // Á¥ØÂä†ÂâçÈù¢ÁªÑÁöÑÂ™í‰ΩìÊï∞Èáè
      for (let i = 0; i < this.currentGroupIndex; i++) {
        position += this.customMediaGroups[i].mediaItems.length;
      }
      // Âä†‰∏äÂΩìÂâçÁªÑÂÜÖÁöÑ‰ΩçÁΩÆ
      return position + this.currentItemIndex + 1;
    },
    
    // ÊòØÂê¶Êúâ‰∏ä‰∏Ä‰∏™Â™í‰Ωì
    hasPrevMedia() {
      return this.currentPosition > 1;
    },
    
    // ÊòØÂê¶Êúâ‰∏ã‰∏Ä‰∏™Â™í‰Ωì
    hasNextMedia() {
      return this.currentPosition < this.totalMediaCount;
    }
  },
  
  methods: {
    // ÂàáÊç¢Ê†áÁ≠æÈ°µ
    switchTab(index) {
      this.currentTab = index;
    },
    
    // ËøõÂÖ•ËØ¶ÊÉÖÊ®°Âºè
    enterDetailMode(groupIndex, itemIndex) {
      this.currentGroupIndex = groupIndex;
      this.currentItemIndex = itemIndex;
      this.isDetailMode = true;
      uni.hideNavigationBarLoading();
    },
    
    // ÈÄÄÂá∫ËØ¶ÊÉÖÊ®°Âºè
    exitDetailMode() {
      this.isDetailMode = false;
      uni.showNavigationBarLoading();
    },
    
    // Ëé∑ÂèñÈªòËÆ§ËßÜÈ¢ëÂ∞ÅÈù¢
    getDefaultVideoCover() {
      // ‰ΩøÁî®Á¨¨‰∏ÄÂº†ÂõæÁâá‰Ωú‰∏∫ÈªòËÆ§ËßÜÈ¢ëÂ∞ÅÈù¢
      return this.customMediaGroups[0]?.mediaItems[0]?.url;
    },
    
    // ‰∏ä‰∏Ä‰∏™Â™í‰Ωì
    prevMedia() {
      if (this.currentItemIndex > 0) {
        // ÂΩìÂâçÁªÑÂÜÖËøòÊúâ‰∏ä‰∏Ä‰∏™
        this.currentItemIndex--;
      } else if (this.currentGroupIndex > 0) {
        // ÂàáÊç¢Âà∞‰∏ä‰∏ÄÁªÑÁöÑÊúÄÂêé‰∏Ä‰∏™
        this.currentGroupIndex--;
        const prevGroup = this.customMediaGroups[this.currentGroupIndex];
        this.currentItemIndex = prevGroup.mediaItems.length - 1;
      }
      this.resetVideoState();
    },
    
    // ‰∏ã‰∏Ä‰∏™Â™í‰Ωì
    nextMedia() {
      const currentGroup = this.customMediaGroups[this.currentGroupIndex];
      if (this.currentItemIndex < currentGroup.mediaItems.length - 1) {
        // ÂΩìÂâçÁªÑÂÜÖËøòÊúâ‰∏ã‰∏Ä‰∏™
        this.currentItemIndex++;
      } else if (this.currentGroupIndex < this.customMediaGroups.length - 1) {
        // ÂàáÊç¢Âà∞‰∏ã‰∏ÄÁªÑÁöÑÁ¨¨‰∏Ä‰∏™
        this.currentGroupIndex++;
        this.currentItemIndex = 0;
      }
      this.resetVideoState();
    },
    
    // ÈáçÁΩÆËßÜÈ¢ëÁä∂ÊÄÅ
    resetVideoState() {
      if (this.currentMedia.type === 'video') {
        this.showVideoControls = true;
      }
    },
    
    // ÂàáÊç¢ËßÜÈ¢ëÊéßÂà∂Ê†èÊòæÁ§∫
    toggleVideoControls() {
      this.showVideoControls = !this.showVideoControls;
    },
    
    // ÂõæÁâáÂä†ËΩΩÈîôËØØÂ§ÑÁêÜ
    handleImageError(item) {
      console.log('ÂõæÁâáÂä†ËΩΩÂ§±Ë¥•:', item.url);
      // ÂèØ‰ª•ËÆæÁΩÆÂ§áÁî®ÂõæÁâá
      // item.url = '/static/default-image.png';
    },
    
    // ÊòæÁ§∫Êìç‰ΩúËèúÂçï
    showActionSheet(groupIndex, itemIndex) {
      const item = this.customMediaGroups[groupIndex].mediaItems[itemIndex];
      const itemList = ['‰øùÂ≠òÂà∞Áõ∏ÂÜå'];
      
      if (item.type === 'video') {
        itemList.push('ÂèëÈÄÅÁªôÊúãÂèã');
      }
      
      uni.showActionSheet({
        itemList: itemList,
        success: (res) => {
          switch (res.tapIndex) {
            case 0:
              this.downloadMedia(item);
              break;
            case 1:
              this.shareMedia(item);
              break;
          }
        },
        fail: (res) => {
          console.log(res.errMsg);
        }
      });
    },
    
    // ‰∏ãËΩΩÂ™í‰Ωì
    downloadMedia(item) {
      uni.showLoading({
        title: item.type === 'image' ? 'ÂõæÁâá‰øùÂ≠ò‰∏≠...' : 'ËßÜÈ¢ë‰øùÂ≠ò‰∏≠...',
        mask: true
      });
      
      uni.downloadFile({
        url: item.url,
        success: (res) => {
          if (res.statusCode === 200) {
            if (item.type === 'image') {
              this.saveImageToPhotosAlbum(res.tempFilePath);
            } else {
              this.saveVideoToPhotosAlbum(res.tempFilePath);
            }
          } else {
            uni.hideLoading();
            uni.showToast({
              title: '‰∏ãËΩΩÂ§±Ë¥•',
              icon: 'none'
            });
          }
        },
        fail: (err) => {
          uni.hideLoading();
          console.error('‰∏ãËΩΩÂ§±Ë¥•:', err);
          uni.showToast({
            title: '‰∏ãËΩΩÂ§±Ë¥•',
            icon: 'none'
          });
        }
      });
    },
    
    // ‰øùÂ≠òÂõæÁâáÂà∞Áõ∏ÂÜå
    saveImageToPhotosAlbum(filePath) {
      uni.saveImageToPhotosAlbum({
        filePath: filePath,
        success: () => {
          uni.hideLoading();
          uni.showToast({
            title: 'ÂõæÁâáÂ∑≤‰øùÂ≠ò',
            icon: 'success'
          });
        },
        fail: (err) => {
          this.handleSaveError(err);
        }
      });
    },
    
    // ‰øùÂ≠òËßÜÈ¢ëÂà∞Áõ∏ÂÜå
    saveVideoToPhotosAlbum(filePath) {
      uni.saveVideoToPhotosAlbum({
        filePath: filePath,
        success: () => {
          uni.hideLoading();
          uni.showToast({
            title: 'ËßÜÈ¢ëÂ∑≤‰øùÂ≠ò',
            icon: 'success'
          });
        },
        fail: (err) => {
          this.handleSaveError(err);
        }
      });
    },
    
    // Â§ÑÁêÜ‰øùÂ≠òÂ§±Ë¥•
    handleSaveError(err) {
      uni.hideLoading();
      console.error('‰øùÂ≠òÂ§±Ë¥•:', err);
      
      if (err.errMsg.includes('auth deny') || err.errMsg.includes('permission denied')) {
        uni.showModal({
          title: 'ÊùÉÈôê‰∏çË∂≥',
          content: 'ËØ∑Âú®ËÆæÁΩÆ‰∏≠ÂºÄÂêØ‰øùÂ≠òÂà∞Áõ∏ÂÜåÁöÑÊùÉÈôê',
          confirmText: 'ÂéªËÆæÁΩÆ',
          success: (res) => {
            if (res.confirm) {
              uni.openSetting();
            }
          }
        });
      } else {
        uni.showToast({
          title: '‰øùÂ≠òÂ§±Ë¥•',
          icon: 'none'
        });
      }
    },
    
    // ÂàÜ‰∫´Â™í‰Ωì
    shareMedia(item) {
      uni.showToast({
        title: 'ÂàÜ‰∫´ÂäüËÉΩÂ∑≤Ëß¶Âèë',
        icon: 'success'
      });
    },
    
    // Êñá‰ª∂ÂàóË°®ÊñπÊ≥ï
    getFileIcon(type) {
      if (type === 'docx') return 'üìÑ';
      if (type === 'pdf') return 'üìÑ';
      if (type === 'xlsx') return 'üìÑ';
      return 'üìÅ';
    },
    
    getIconColor(type) {
      const colors = {
        docx: '#8dd4d4',
        pdf: '#ff90bb',
        xlsx: '#ffff7f',
        default: '#e3f2fd'
      };
      return colors[type] || colors.default;
    },

    previewFile(file) {
      if (file.type === 'pdf') {
        uni.showToast({ 
          title: 'ÂáÜÂ§áÈ¢ÑËßàPDFÊñá‰ª∂', 
          icon: 'none' 
        });
      } else {
        uni.showModal({
          title: 'ÊèêÁ§∫',
          content: 'ÂæÆ‰ø°ÊöÇ‰∏çÊîØÊåÅÁõ¥Êé•È¢ÑËßàËØ•Ê†ºÂºèÔºåÂª∫ËÆÆÁÇπÂáªÊü•ÁúãÊåâÈíÆË∑≥ËΩ¨Êü•Áúã',
          showCancel: false
        });
      }
    },

    viewFile(file) {
      // ‰ºòÂÖàÂ§ÑÁêÜÂú®Á∫øÈ¢ÑËßà
      if (file.url) {
        openDocumentFromUrl({ url: file.url, type: file.type })
        return
      }
      
      // Â§ÑÁêÜtargetPageÊò†Â∞ÑÂà∞Âú®Á∫øÈ¢ÑËßà
      if (file.targetPage) {
        const mapping = {
          '/pages/issue1/issue1561': { 
            url: 'https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/%E4%B8%80%E6%9C%9F/%E6%B4%BB%E5%8A%A8%E6%89%A7%E8%A1%8C%E8%BF%87%E7%A8%8B/%E6%B1%87%E6%8A%A5%E4%BC%9A/%E2%80%9C%E6%B4%9E%E8%A7%81%E5%8E%86%E5%8F%B2%E2%80%9D%E9%A1%B9%E7%9B%AE%E5%88%B6%E5%AD%A6%E4%B9%A0%E6%B1%87%E6%8A%A5%E4%BC%9A%E8%AE%AE%E7%A8%8B.pdf', 
            type: 'pdf' 
          },
          '/pages/issue1/issue1562': { 
            url: 'https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/%E4%B8%80%E6%9C%9F/%E6%B4%BB%E5%8A%A8%E6%89%A7%E8%A1%8C%E8%BF%87%E7%A8%8B/%E6%B1%87%E6%8A%A5%E4%BC%9A/%E6%B1%87%E6%8A%A5%E4%BC%9A10.28%E5%88%86%E5%B7%A5%E5%90%8D%E5%8D%95.xlsx', 
            type: 'xlsx' 
          },
          '/pages/issue1/issue1563': { 
            url: 'https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/%E4%B8%80%E6%9C%9F/%E6%B4%BB%E5%8A%A8%E6%89%A7%E8%A1%8C%E8%BF%87%E7%A8%8B/%E6%B1%87%E6%8A%A5%E4%BC%9A/%E8%B7%AF%E7%BA%BF%E6%8C%87%E7%A4%BA%E7%89%8C.docx', 
            type: 'docx' 
          }
        }
        const target = mapping[file.targetPage]
        if (target && target.url) {
          openDocumentFromUrl(target)
          return
        }
        
        // Â¶ÇÊûúÊò†Â∞Ñ‰∏≠Ê≤°ÊúâÊâæÂà∞ÔºåÂ∞ùËØïË∑≥ËΩ¨Âà∞È°µÈù¢
        uni.navigateTo({
          url: file.targetPage,
          fail: (err) => {
            console.error('Ë∑≥ËΩ¨Â§±Ë¥•:', err)
            uni.showToast({ title: 'È°µÈù¢‰∏çÂ≠òÂú®', icon: 'none' })
          }
        })
        return
      }
      
      // Â¶ÇÊûúÊó¢Ê≤°Êúâurl‰πüÊ≤°ÊúâtargetPage
      uni.showToast({ title: 'ÊöÇÊó†Âú®Á∫øÈ¢ÑËßàÔºåËØ∑Á®çÂêé', icon: 'none' })
    }
  },
  
  onShareAppMessage() {
    if (this.currentTab === 0) {
      return {
        title: this.currentMedia.title || (this.currentMedia.type === 'image' ? 'ÂàÜ‰∫´ÂõæÁâá' : 'ÂàÜ‰∫´ËßÜÈ¢ë'),
        path: '/pages/media-gallery/media-gallery',
        imageUrl: this.currentMedia.type === 'image' 
          ? this.currentMedia.url 
          : (this.currentMedia.coverUrl || this.getDefaultVideoCover())
      };
    } else {
      return {
        title: 'ÂàÜ‰∫´Êñá‰ª∂ÂàóË°®',
        path: '/pages/media-gallery/media-gallery'
      };
    }
  }
};
